<template>
  <tas-base-crud :config="config" hide-create hide-update hide-delete hide-filter hide-detail>
    <template v-slot:list-table-action="data">
      <a @click="getSlaveList(data.rowData)" class="btn btn-info btn-sm">Lihat Daftar Komponen</a>
    </template>
    <!-- <template v-slot:list-count_component_active="data">
      <span v-if="data.rowData>0" class="label label-lg label-outline-info label-inline">{{ data.rowData }} Komponen</span>
      <span class="font-size-sm font-italic text-muted" v-else>Belum ada komponen aktif</span>
    </template>
    <template v-slot:list-count_component_mapped_fina="data">
      <span v-if="data.rowData>0" class="label label-lg label-outline-success label-inline">{{ data.rowData }} Komponen</span>
      <span class="font-size-sm font-italic text-muted" v-else>Belum ada komponen terintegrasi</span>
    </template> -->
  </tas-base-crud>
</template>

<script>
export default {
  name: 'crud-mapping-upah-lembur',
  data () {
    return {
      config: {
        title: 'Mapping Akun Lembur',
        model_api: null,
        getter: 'custom/mapping-upah-lembur/list-job-position-categories',
        setter: 'custom/mapping-upah-lembur/list-job-position-categories',
        custom_api: {
          list: 'custom/mapping-upah-lembur/list-job-position-categories',
          read: 'custom/mapping-upah-lembur/show-job-position-categories'
        },
        pk_field: 'name',
        permission: {
          create: 'create-mapping-upah-lembur',
          read: 'view-mapping-upah-lembur',
          update: 'update-mapping-upah-lembur',
          delete: 'delete-mapping-upah-lembur'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'name', label: 'Nama Kategori Jabatan', methods: { list: true, detail: true, create: true, update: true, filter: false } },
          {
            id: 'employment_level_id',
            label: 'Level Ketenagakerjaan',
            methods: {
              list: { view_data: 'rel_employment_level_id' },
              detail: { view_data: 'rel_employment_level_id' },
              create: {
                setter: 'master-employment-levels',
                getter: 'master-employment-levels',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              },
              update: {
                setter: 'master-employment-levels',
                getter: 'master-employment-levels',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              },
              filter: {
                setter: 'master-employment-levels',
                getter: 'master-employment-levels',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              }
            }
          }
        ]
      }
    }
  },
  methods: {
    getSlaveList (e) {
      let slaveList = []
      let def = {
        name: 'Mapping Akun Penggajian',
        module: 'payroll/mapping-upah-lembur',
        as_param: 'job_position_category_id',
        key_field: 'id',
        overwrite: {
          fields: [
            { rule: '1.methods.list', value: false },
            { rule: '1.methods.filter', value: false },
            { rule: '1.methods.create', value: false },
            { rule: '1.methods.update', value: false },
            { rule: '1.methods.detail', value: false }
          ]
        }
      }
      this.$_api.get('custom/mapping-upah-lembur/show-job-position-categories', { id: e.id }).then(res => {
        const status = res.data.data_status_employments
        if (status.length) {
          for (let i = 0; i < status.length; i++) {
            const el = status[i]
            slaveList.push({
              name: el.name,
              module: 'payroll/master/mapping-upah-lembur',
              as_param: 'job_position_category_id',
              key_field: 'id',
              overwrite: {
                filter_api: { 'status_employment_id': el.id },
                custom_api: {
                  read: 'custom/mapping-upah-lembur/show?status_employment_id=' + el.id + '&job_position_category_id=' + e.id
                },
                fields: [
                  { rule: '1.methods.list', value: false },
                  { rule: '1.methods.filter', value: false },
                  { rule: '1.methods.create', value: false },
                  { rule: '1.methods.update', value: false },
                  { rule: '1.methods.detail', value: false }
                ]
              }
            })
          }
        } else slaveList.push(def)
        this.$set(this.config, 'slave', slaveList)
        this.$nextTick().then(f => {
          this.$children[0].getEvent('detail', e)
        })
      }).catch((err) => {
        console.log(err)
        this.$_alert.error(err)
      })
    }
  }
}
</script>



